// src/components/TopBar.vue
<template>
  <div class="top-bar" :class="{ 'hidden': isTopBarHidden }">
    <div class="top-bar-content">
      <button class="menu-button" @click="toggleSidebar">
        <font-awesome-icon :icon="['fas', isSidebarOpen ? 'times' : 'bars']" />
      </button>
      <h1 class="page-title" :class="{ 'visible': showTitleInTopBar }">{{ pageTitle }}</h1>
      <button v-if="isBlogPost" class="toc-button" @click="toggleTocSidebar">
        <font-awesome-icon :icon="['fas', isTocSidebarOpen ? 'times' : 'list']" />
      </button>
    </div>
  </div>
</template>

<script>
import { useStore } from 'vuex'
import { computed, ref, onMounted, onUnmounted, watch } from 'vue'
import { useRoute } from 'vue-router'

export default {
  name: 'TopBar',
  setup() {
    const store = useStore()
    const route = useRoute()

    const isTopBarHidden = computed(() => store.state.content.topBarHidden)
    const isSidebarOpen = computed(() => store.state.content.sidebarOpen)
    const isTocSidebarOpen = computed(() => store.state.content.tocSidebarOpen)
    const showTitleInTopBar = computed(() => store.state.content.showTitleInTopBar)
    const isBlogPost = computed(() => route.name === 'BlogPost')

    const lastScrollTop = ref(0)
    let hideTimeout = null
    const hideDelay = 3000 // 3 seconds
    let animationFrameId = null

    const setHideTimeout = () => {
      clearTimeout(hideTimeout)
      hideTimeout = setTimeout(() => {
        if (!isSidebarOpen.value && !isTocSidebarOpen.value) {
          store.dispatch('content/setTopBarHidden', true)
          console.log('Top bar hidden due to inactivity')
        }
      }, hideDelay)
    }

    const checkScroll = () => {
      const st = window.pageYOffset || document.documentElement.scrollTop
      
      if (Math.abs(st - lastScrollTop.value) > 5) {
        console.log('Scroll detected - current position:', st, 'last position:', lastScrollTop.value)

        if (st <= 0 || st < lastScrollTop.value) {
          // Scrolling up or at the top
          store.dispatch('content/setTopBarHidden', false)
          console.log('Top bar shown - scrolling up or at top')
          setHideTimeout()
        } else if (st > lastScrollTop.value && st > 100) {
          // Scrolling down and not at the top
          store.dispatch('content/setTopBarHidden', true)
          console.log('Top bar hidden - scrolling down')
        }

        lastScrollTop.value = st <= 0 ? 0 : st
      }

      animationFrameId = requestAnimationFrame(checkScroll)
    }

    const showTopBar = () => {
      store.dispatch('content/setTopBarHidden', false)
      console.log('Top bar shown')
      setHideTimeout()
    }

    onMounted(() => {
      animationFrameId = requestAnimationFrame(checkScroll)
      showTopBar()
      console.log('Top bar component mounted')
    })

    onUnmounted(() => {
      cancelAnimationFrame(animationFrameId)
      clearTimeout(hideTimeout)
      console.log('Top bar component unmounted')
    })

    const toggleSidebar = () => {
      store.dispatch('content/toggleSidebar')
      showTopBar()
    }

    const toggleTocSidebar = () => {
      store.dispatch('content/toggleTocSidebar')
      showTopBar()
    }

    const pageTitle = computed(() => {
      if (route.name === 'BlogPost') {
        return store.state.content.currentBlogPostTitle || 'Blog Post'
      }
      return route.meta.title || ''
    })

    // Watch for changes in isTopBarHidden
    watch(isTopBarHidden, (newValue) => {
      console.log('Top bar hidden state changed:', newValue)
      if (!newValue) {
        setHideTimeout()
      }
    })

    return {
      isTopBarHidden,
      isSidebarOpen,
      isTocSidebarOpen,
      toggleSidebar,
      toggleTocSidebar,
      pageTitle,
      showTitleInTopBar,
      isBlogPost
    }
  }
}
</script>






















<style scoped>
.top-bar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background-color: var(--topbar-background);
  height: var(--topbar-height);
  transition: transform 0.3s ease-in-out;
  z-index: var(--z-index-topbar);
}

.top-bar.hidden {
  transform: translateY(-100%);
}

.top-bar-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  padding: 0 var(--spacing-medium);
}

.menu-button, .toc-button {
  background: none;
  border: none;
  font-size: 1.2rem;
  color: var(--text-color);
  cursor: pointer;
}

.page-title {
  margin: 0;
  font-size: 1.2rem;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.page-title.visible {
  opacity: 1;
}
</style>
