<!-- src/App.vue -->
<template>
  <div id="app" :class="{ 'dark-mode': isDarkMode, 'topbar-hidden': isTopBarHidden }">
    <TopBar />
    <div class="content-wrapper">
      <Sidebar />
      <MainContent />
    </div>
  </div>
</template>

<script>
import { computed } from 'vue'
import { useStore } from 'vuex'
import TopBar from './components/TopBar.vue'
import Sidebar from './components/Sidebar.vue'
import MainContent from './components/MainContent.vue'

export default {
  name: 'App',
  components: {
    TopBar,
    Sidebar,
    MainContent
  },
  setup() {
    const store = useStore()
    const isDarkMode = computed(() => store.state.theme.isDarkMode)
    const isTopBarHidden = computed(() => store.state.content.topBarHidden)

    return {
      isDarkMode,
      isTopBarHidden
    }
  }
}
</script>

<style>
@import './assets/css/theme.css';

body, html {
  margin: 0;
  padding: 0;
  height: 100%;
  overflow-x: hidden;
}

#app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.content-wrapper {
  flex: 1;
  display: flex;
  position: relative;
}

#app:not(.topbar-hidden) .content-wrapper {
  margin-top: var(--topbar-height);
}

#app.topbar-hidden .content-wrapper {
  margin-top: 0;
}
</style>
